function* UpdateWorkflow(action) {
  try {
    yield put(
      setLoading({
        type: "update",
        status: true,
      })
    );
    yield updateWorkflowAPI(action.payload);
    yield put(
      setLoading({
        type: "update",
        status: false,
      })
    );
    Notification("success", "Update successfully!");
    yield delay(500);
    window.open(`${FE_URL}/workflows`, "_self");
  } catch (error) {
    yield put(
      setLoading({
        type: "update",
        status: false,
      })
    );
    Notification("error", error.response.data.error);
  }
}

