function* CreateAction(action) {
  try {
    yield put(
      setLoading({
        type: "modal",
        status: true,
      })
    );
    yield createActionAPI(action.payload);
    yield put(
      setLoading({
        type: "modal",
        status: false,
      })
    );
    Notification("success", "Create successfully!");
    yield put(setStatus("Create action successfully!"));
  } catch (error) {
    yield put(
      setLoading({
        type: "modal",
        status: false,
      })
    );
    Notification("error", error.response.data.error);
  }
}

