export const optionsParse = {
  replace: (domNode) => {
    if (_.get(domNode, "name", "") === "style") {
      return <style></style>;
    }

    if (
      _.get(domNode, "name", "") !== "" &&
      !allowedTags.includes(_.get(domNode, "name", ""))
    ) {
      return (
        <>
          {"< "}
          {_.get(domNode, "name", "")}
          {" >"}
          {_.map(_.get(domNode, "children", []), (item) =>
            _.get(item, "data", "")
          )}
        </>
      );
    }
  },
};

