export const handlerIconComp = (type) => {
  switch (type) {
    case "details":
    case "DETAILS":
    case "multi_detail":
    case "new_detail":
      return detailsImg;
    case "call_center":
    case "table":
    case "TABLE":
    case "ticket_list_call_center":
      return tableImg;
    case "tags":
      return tagsImg;
    case "tab":
      return tabsImg;
    case "knowledge":
      return KnowledgeBaseImg;
    case "form":
      return InteractionFormImg;
    case "iframe":
      return IframeIcon;
    default:
      break;
  }
};

export const isValid_Id = (_id) => {
  return /^[0-9a-fA-F]{24}$/.test(_id);
};
