function* LoadNewInteraction(action) {
  try {
    const response = yield loadNewInteractionAPI(action.payload);

    if (
      JSON.parse(localStorage.getItem("@ucrm_callCenter_interactionDetails"))
    ) {
      const newInteraction = [
        ...JSON.parse(
          localStorage.getItem("@ucrm_callCenter_interactionDetails")
        ),
        { ...response.data.data },
      ];

      localStorage.setItem(
        "@ucrm_callCenter_interactionDetails",
        JSON.stringify(newInteraction)
      );
    } else {
      localStorage.setItem(
        "@ucrm_callCenter_interactionDetails",
        JSON.stringify([{ ...response.data.data }])
      );
    }
  } catch (error) {}
}
