  yield takeLatest(interactionConfig.type, LoadDetailsInteractionConfig);
  yield takeLatest(deleteInteractionConfig.type, DeleteConfigInteraction);
  yield takeLatest(loadPageTitle.type, LoadPageTitleSaga);

